.admin-customers {
  padding: 20px;
}

.customers-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
}

.customers-info {
  color: var(--text-secondary);
  font-size: 14px;
  margin: 0;
}

.customers-table-container {
  background: var(--bg-primary);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-light);
  overflow: visible;
}

.customers-table {
  width: 100%;
  border-collapse: collapse;
}

.customers-table th {
  background: var(--bg-tertiary);
  padding: 12px 16px;
  text-align: left;
  font-weight: 600;
  color: var(--text-primary);
  font-size: 13px;
  border-bottom: 1px solid var(--border-color);
}

.customers-table td {
  padding: 12px 16px;
  border-bottom: 1px solid var(--border-light);
  color: var(--text-secondary);
  font-size: 14px;
  vertical-align: middle;
}

.customers-table tbody tr:hover {
  background: var(--bg-secondary);
}

.customer-avatar {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 38px;
  height: 38px;
  background: linear-gradient(135deg, #5c5c5c 0%, #515152 100%);
  color: white;
  font-size: 1.2rem;
  font-weight: 600;
  border-radius: 50%;
  margin-right: 12px;
  float: left;
}

.customer-info {
  display: inline-block;
  vertical-align: top;
}

.customer-name {
  font-weight: 600;
  color: #1f2937;
}

.customer-id {
  font-size: 0.8rem;
  color: #9ca3af;
}

.status-badge {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: capitalize;
}

.status-active {
  background: #dcfce7;
  color: #166534;
}

.status-inactive {
  background: #fee2e2;
  color: #991b1b;
}

.actions {
  position: relative;
}

.actions-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #6b7280;
  cursor: pointer;
  padding: 0 8px;
  border-radius: 50%;
  transition: background 0.2s ease;
}

.actions-btn:hover {
  background: #f3f4f6;
}

.actions-menu {
  position: absolute;
  right: 0;
  top: 35px;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
  min-width: 160px;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  padding: 8px 0;
}

.actions-menu button {
  background: none;
  border: none;
  color: #374151;
  font-size: 0.95rem;
  text-align: left;
  padding: 10px 20px;
  cursor: pointer;
  transition: background 0.2s;
  display: flex;
  align-items: center;
  gap: 8px;
}

.actions-menu button:hover {
  background: #f3f4f6;
}

.actions-menu .delete {
  color: var(--error-color);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}

.modal-content {
  background: var(--bg-primary);
  border-radius: var(--radius-lg);
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: var(--shadow-xl);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid var(--border-light);
}

.modal-header h3 {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
}

.close-btn {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: var(--text-light);
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn:hover {
  color: var(--text-primary);
}

.modal-body {
  padding: 20px;
}

.detail-row {
  margin-bottom: 12px;
  padding: 8px 0;
  border-bottom: 1px solid var(--border-light);
}

.detail-row:last-child {
  border-bottom: none;
}

.modal-actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  margin-top: 20px;
}

@media (max-width: 768px) {
  .customers-header {
    flex-direction: column;
    gap: 15px;
    align-items: stretch;
  }
  .customers-table th, .customers-table td {
    padding: 10px 8px;
  }
}